.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.MuiAppBar-colorPrimary {
  background-color: rgb(119, 136, 153) !important;
}

.MuiDialog-root {
  top: 90px !important;
}

.Layout-content-7 {
  padding: 20px !important;
}

.success > .MuiSnackbarContent-root {
  background-color: rgb(48, 202, 55) !important;
}

.validation > .MuiSnackbarContent-root {
  background-color: rgb(241, 82, 82) !important;
}

.error > .MuiSnackbarContent-root {
  background-color: rgb(241, 82, 82) !important;
}
.info > .MuiSnackbarContent-root {
  background-color: rgb(141, 149, 156) !important;
}
.warning > .MuiSnackbarContent-root {
  background-color: rgb(221, 233, 54) !important;
}
.tblCell-primary {
  background-color: rgb(119, 136, 153);
  color: rgb(60, 70, 75);
}
.loading {
  position: fixed;
  top: 30px;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: #8d959c;
  opacity: 0.7;
  z-index: 9999;
}
.MuiSelect-root ul {
  white-space: normal;
}

.border-red{
  border: 1px solid red;
}

.border-none{
  border: none;
}

.MuiList-root li {
  white-space: normal;
}
.form-btn-container {
  display: flex;
  position: sticky;
  position: -webkit-sticky; /* Safari, android mobile browsers */
  flex-direction: row;
  flex-flow: row;
  flex: 1;
  justify-content: flex-start;
  height: 60px;
  bottom: 0px;
  z-index: 99;
  width: auto;
  border: 1px solid #c0c0c0;
  background-color: #f9f9f9;
}
canvas.drawing,
canvas.drawingBuffer {
  position: absolute;
  left: 0;
  top: 0;
}

@media only screen and (max-width: 600px) {
  .Layout-content-7 {
    padding: 6px !important;
  }
}
