.pop-modal {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 700px;
}
.paper {
    display: inline;
    position: relative;
    border: 1px solid #000;
    box-shadow: 0px 3px 5px -3px rgba(0, 0, 0, 0.2),
    0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 13px 24px 0px rgba(0, 0, 0, 0.12);
    background-color: #fff;
    height: 80%;
    width: 80%;
}
